import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {YourDestinationComponent} from './your-destination.component';
import {YourDestinationGuard} from './your-destination.guard';

/**
 * Adjust module routing here.
 */
const routes: Routes = [
    {
        path: '',
        component: YourDestinationComponent,
        canActivate: [YourDestinationGuard]
    }
];

@NgModule({
    imports: [
        RouterModule.forChild(routes),
    ],
    exports: [RouterModule],
})

export class YourDestinationRoutingModule {
}
